<!-- Header.vue -->
<template>
    <div class="header">
      <button @click="logout">Logout</button>
     
      <button @click="viewBookedTickets">Booked Tickets</button>
    </div>
  </template>
  
  <script>
  export default {
    methods: {
      logout() {
      // Clear user authentication state (example: remove access token)
      localStorage.removeItem('access_token');
      
      // Clear user-specific data (optional, based on your app's requirements)
      // this.$store.commit('clearUserData'); // If using Vuex for state management

      // Redirect to the login page
      this.$router.push('/');
    },
  
     
  
      viewBookedTickets() {
        try {
           
            this.$router.push('/booked')
        } catch (error) {
          console.error(error);
        }
      },
    },
  };
  </script>
  
  <style>
  /* Add your CSS styles for the header here */
  .header {
    /* Your header styles here */
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f2f2f2;
    padding: 10px;
  }
  .header button {
    margin-right: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  </style>
  