<template>
  <div>
    <h1>Login</h1>
    <p v-if="error" class="error">{{ error }}</p>
    <form @submit.prevent="submitForm">
      <div>
        <label for="username">Username:</label>
        <input type="text" id="username" v-model="username" required>
      </div>
      <div>
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="password" required>
      </div>
      <div>
        <button type="submit">Login</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  data() {
    return {
      username: '',
      password: '',
      error: ''
    };
  },
  methods: {
    submitForm() {
      const userData = {
        username: this.username,
        password: this.password
      };

 
      // Send a POST request to the backend API
      axios
        .post('http://127.0.0.1:5000/login', userData
        )
        .then(response => {
          console.log(response.data);
           
    const { username, roles, access_token } = response.data;

    // Save the user data and access token in localStorage
    localStorage.setItem('access_token', access_token);
    localStorage.setItem('username', username);
    localStorage.setItem('roles', JSON.stringify(roles));

          // Check if the user is an admin
          if (response.data.roles.includes('admin')) {
            // If admin, navigate to AdminPage.vue
            this.$router.push('/AdminPage');
          } else {
            // If not admin, navigate to some other page (e.g., HomePage.vue)
            this.$router.push('/UserDashboard');
          }
        })
        .catch(error => {
          // Error handling
          if (error.response) {
            // The request was made, but the server responded with an error status
            console.error('Error status:', error.response.status);
            console.error('Error message:', error.response.data.error);
            this.error = 'Invalid username or password';
          } else if (error.request) {
            // The request was made, but no response was received
            console.error('No response received..:', error.request);
            this.error = 'No response received';
          } else {
            // Something happened in setting up the request
            console.error('Error setting up the request:', error.message);
            this.error = 'An error occurred';
          }
        });
    }
  }
};
</script>

<style>
.error {
  color: red;
  margin-top: 10px;
}
</style>
