<template>
    <div>
      <h1>Admin Page</h1>
      <div>
          <button @click="showManagement">showManagement</button>
          <button @click="ExportCSV">Export Theatre Data</button>
          <button @click="PromoteToAdmin">Promote To Admin</button>
         </div>
      <div>
        <h2>Create Theatre</h2>
        <form @submit.prevent="createTheatre">
          <div>
            <label for="name">Name:</label>
            <input type="text" id="name" v-model="theatreName" required>
          </div>
          <div>
            <label for="place">Place:</label>
            <input type="text" id="place" v-model="theatrePlace" required>
          </div>
          <div>
            <label for="capacity">Capacity:</label>
            <input type="number" id="capacity" v-model="theatreCapacity" required>
          </div>
          <div>
            <button type="submit">Create Theatre</button>
          </div>
          <div>
          <button @click="viewTheatres">View Available Theatres</button>
         </div>
        </form>
      </div>
      
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        theatreName: '',
        theatrePlace: '',
        theatreCapacity: '',
      
      };
    },
    methods: {
      createTheatre() {
        const newTheatreData = {
          name: this.theatreName,
          place: this.theatrePlace,
          capacity: this.theatreCapacity,
        };
  
        axios
          .post('http://127.0.0.1:5000/admin/theatres', newTheatreData, {
            headers: { Authorization: `Bearer ${localStorage.getItem('access_token')}` },
          })
          .then((response) => {
            // Theatre created successfully, do something (e.g., show success message)
            console.log('Theatre created successfully!');
         console.log('Server response:', response.data);
          })
          .catch((error) => {
            // Handle errors (e.g., show error message)
            console.error('Error creating theatre:', error.message);
          });
      },
      // Other methods for updating and deleting theatres and shows
      viewTheatres() {
      this.$router.push('/theatres');
    },
    ExportCSV() {
      this.$router.push('/ExportCSV');
    },
    showManagement() {
      this.$router.push('/showManagement');
    },
    PromoteToAdmin() {
      this.$router.push('/PromoteToAdmin');
    }
    },
  };
  </script>
  <style>
  /* CSS styles go here */
  </style>