<template>
    <div>
      <!-- ... -->
      <div v-if="showFormVisible">
        <h2>Add Show</h2>
        <form @submit.prevent="createShow">
          <div>
            <label for="name">Name:</label>
            <input type="text" id="name" v-model="show.name" required>
          </div>
          <div>
            <label for="rating">Rating:</label>
            <input type="number" id="rating" v-model="show.rating" min="1" max="5" required>
          </div>
          <div v-for="genre in genres" :key="genre.value">
            <input
              type="checkbox"
              :id="genre.value"
              :value="genre.value"
              v-model="show.genres"
             
            >
            <label :for="genre.value">{{ genre.label }}</label>
          </div>
          <div>
            <label for="ticketPrice">Ticket Price:</label>
            <input type="number" id="ticket_price" v-model="show.ticket_price" required>
          </div>
          <div>
            <button type="submit">Add Show</button>
          </div>
        </form>
      </div>
    </div>
  </template>
  
  
<script>
export default {
  data() {
    return {
      // ... (other data)
      showFormVisible: true,
      show: {
        name: '',
        rating: 0,
        genres: [],
        ticket_price: 0,
      },
      genres: [
        { label: 'Comedy', value: 'comedy' },
        { label: 'Action', value: 'action' },
        { label: 'Sci-Fi', value: 'scifi' },
        { label: 'Romance', value: 'romance' },
        { label: 'Horror', value: 'horror' },
        { label: 'Drama', value: 'drama' },
        { label: 'Fantasy', value: 'fantasy' },
      ],
    };
  },
  mounted() {
    console.log('showpage component mounted');
  },
  methods: {
    // ... (other methods)
    async createShow() {
      console.log('createShow method called');
      const theatreid = this.$route.params.theatreid;
      try {
        const response = await fetch(`http://127.0.0.1:5000/admin/theatres/${theatreid}/add_show`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('access_token')}`
          },
          body: JSON.stringify(this.show),
        });

        if (!response.ok) {
          throw new Error('Failed to add show');
        }

        console.log('successful');
        // Show added successfully
        // You can display a success message or redirect to another page
        // (e.g., back to the list of shows)
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>