<template>
  <div id="app">
    <h1>Welcome to the Ticket Booking App</h1>
    <p>This is the homepage of the ticket booking app.</p>
    <p>You can login or register to start booking tickets.</p>
    <router-link v-if="!isLoggedIn" :to="{ path: '/login' }">Login</router-link>
    <router-link v-else-if="isAdmin" :to="{ path: '/AdminPage' }">Admin Dashboard</router-link>
    <router-link v-else :to="{ path: '/UserDashboard' }">User Dashboard</router-link>
    <div></div>
    <a href="/register">Register</a>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    isLoggedIn() {
      // Check if the access_token is present in localStorage
      return localStorage.getItem('access_token') !== null;
    },
    isAdmin() {
      // Check if the user has the admin role (modify this condition based on your role system)
      const userRoles = localStorage.getItem('roles');
      return userRoles && userRoles.includes('admin');
    },
  },
};
</script>

  